<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-说明1" class="vue-doc-element">说明</h2>
<p class="vue-doc-element">Transfer 组件主要基于以下四个 API 来使用：</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <ul class="vue-doc-element">
<li>:data：总体数据，数组，每项为一个对象，且必须含有 key 值，组件基于此做索引。</li>
<li>:target-keys：目标列索引集合，数组，每项为数据的 key 值，Transfer 会将含有这些 key 值的数据筛选到右边。</li>
<li>:render-format：每行数据显示的格式函数，默认优先显示 label 值，没有时显示 key 值，可以自己组合出需要的数据格式。</li>
<li>@on-change：当点击转移按钮时，组件本身并不会转移数据，而是触发事件，由用户来操作数据。</li>
</ul>
<p class="vue-doc-element">示例：</p>
<pre class="vue-doc-element"><code class="lang-js">export <span class="hljs-keyword">default</span> {
    <span class="hljs-keyword">data</span> () {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-keyword">data</span>: [
                { <span class="hljs-string">"key"</span>: <span class="hljs-string">"1"</span>, <span class="hljs-string">"label"</span>: <span class="hljs-string">"Content 1"</span>, <span class="hljs-string">"disabled"</span>: <span class="hljs-literal">false</span> },
                { <span class="hljs-string">"key"</span>: <span class="hljs-string">"2"</span>, <span class="hljs-string">"label"</span>: <span class="hljs-string">"Content 2"</span>, <span class="hljs-string">"disabled"</span>: <span class="hljs-literal">true</span> },
                { <span class="hljs-string">"key"</span>: <span class="hljs-string">"3"</span>, <span class="hljs-string">"label"</span>: <span class="hljs-string">"Content 3"</span>, <span class="hljs-string">"disabled"</span>: <span class="hljs-literal">false</span> }
            ],
            targetKeys: [<span class="hljs-string">"1"</span>,<span class="hljs-string">"2"</span>]
        }
    },
    methods: {
        render (item) {
            <span class="hljs-keyword">return</span> item.key + <span class="hljs-string">':'</span> + item.label;
        },
        onChange (newTargetKeys) {
            <span class="hljs-keyword">this</span>.targetKeys = newTargetKeys;
        }
    }
}
</code></pre>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        


    </div>
  </template>
  <script>
      
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


