<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-说明1">说明</a></li><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-搜索1">搜索</a></li><li><a href="#vue-doc-自定义渲染行数据1">自定义渲染行数据</a></li><li><a href="#vue-doc-高级用法1">高级用法</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-transfer穿梭框1" class="vue-doc-element">Transfer 穿梭框</h1>
<p class="vue-doc-element">双栏穿梭选择框，常用于将多个项目从一边移动到另一边。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-null1" class="vue-doc-element">null</h1>
<p class="vue-doc-element">null</p>

        </div>

        <div class="vue-doc-components">
          <demodesc></demodesc>
<demobasic></demobasic>
<demosearch></demosearch>
<democustom></democustom>
<demoadvanced></demoadvanced>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-tooltipprops1_1" class="vue-doc-element">Tooltip props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>数据源，其中的数据将会被渲染到左边一栏中，targetKeys 中指定的除外。</td>
<td>Array</td>
<td>[ ]</td>
</tr>
<tr>
<td>targetKeys</td>
<td>显示在右侧框数据的key集合</td>
<td>Array</td>
<td>[ ]</td>
</tr>
<tr>
<td>render-format</td>
<td>每行数据渲染函数，该函数的入参为 data 中的项</td>
<td>Function</td>
<td>默认显示label，没有时显示key</td>
</tr>
<tr>
<td>selected-keys</td>
<td>设置哪些项应该被选中</td>
<td>Array</td>
<td>[ ]</td>
</tr>
<tr>
<td>list-style</td>
<td>两个穿梭框的自定义样式</td>
<td>Object</td>
<td>{ }</td>
</tr>
<tr>
<td>titles</td>
<td>标题集合，顺序从左至右</td>
<td>Array</td>
<td>[&#39;源列表&#39;, &#39;目的列表&#39;]</td>
</tr>
<tr>
<td>operations</td>
<td>操作文案集合，顺序从上至下</td>
<td>Array</td>
<td>[ ]</td>
</tr>
<tr>
<td>filterable</td>
<td>是否显示搜索框</td>
<td>Boolean</td>
<td>Boolean</td>
</tr>
<tr>
<td>filter-placeholder</td>
<td>搜索框的占位</td>
<td>String</td>
<td>请输入搜索内容</td>
</tr>
<tr>
<td>filter-method</td>
<td>自定义搜索函数，入参为 data 和 query，data 为项，query 为当前输入的搜索词</td>
<td>Function</td>
<td>默认搜索label</td>
</tr>
<tr>
<td>not-found-text</td>
<td>当列表为空时显示的内容</td>
<td>String</td>
<td>列表为空</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-transferevents1_2" class="vue-doc-element">Transfer events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-popper-show</td>
<td>在提示框显示时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-popper-hide</td>
<td>在提示框消失时触发</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-tooltipslot1_3" class="vue-doc-element">Tooltip slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>选项在两栏之间转移时的回调函数</td>
<td>targetKeys, direction, moveKeys</td>
</tr>
<tr>
<td>on-selected-change</td>
<td>选中项发生变化时触发</td>
<td>sourceSelectedKeys, targetSelectedKeys</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-transferslot1_4" class="vue-doc-element">Transfer slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>自定义底部内容</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demodesc from './demo/desc.md';
import demobasic from './demo/basic.md';
import demosearch from './demo/search.md';
import democustom from './demo/custom.md';
import demoadvanced from './demo/advanced.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demodesc,demobasic,demosearch,democustom,demoadvanced
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


